<template>
  <div class="baima-container">
    <advert></advert>
    <div class="register-container">
      <language></language>
      <div class="register-inner">
        <el-form class="register-form" autoComplete="on" :model="registerForm" :rules="registerRules" ref="registerForm"
                 label-position="left">
          <el-form-item :label="$t('register.telOrEmail')" prop="username" :class="{'noLabel': !registerForm.username, 'onFocus':registerType.username === 1}">
            <el-input name="username" type="text" v-model="registerForm.username" :placeholder="$t('register.telOrEmail')" @focus="registerType.username = 1" @blur="registerType.username = 0"/>
          </el-form-item>
          <el-form-item :label="$t('register.password')" prop="password" :class="{'noLabel': !registerForm.password, 'onFocus':registerType.password === 1}">
            <el-input name="password" :type="pwdType" @keyup.enter.native="handleLogin" v-model="registerForm.password"
                      :placeholder="$t('register.password')" @focus="registerType.password = 1" @blur="registerType.password = 0"></el-input>
            <span class="show-pwd" @click="showPwd" v-if="registerForm.password">
            <img src="../../images/ico-pass_hidden.png" width="24" alt="" v-if="pwdType">
            <img src="../../images/ico-pass_show.png" width="24" alt="" v-else>
          </span>
          </el-form-item>
          <el-form-item :label="$t('register.checkPassword')" prop="checkPassword" :class="{'noLabel': !registerForm.checkPassword, 'onFocus':registerType.checkPassword === 1}">
            <el-input name="checkPassword" :type="pwdType" @keyup.enter.native="handleLogin" v-model="registerForm.checkPassword"
                      :placeholder="$t('register.checkPassword')" @focus="registerType.checkPassword = 1" @blur="registerType.checkPassword = 0"></el-input>
            <span class="show-pwd" @click="showPwd" v-if="registerForm.checkPassword">
            <img src="../../images/ico-pass_hidden.png" width="24" alt="" v-if="pwdType">
            <img src="../../images/ico-pass_show.png" width="24" alt="" v-else>
          </span>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" style="width:100%;" :loading="loading" @click.native.prevent="handleRegister"
                       :disabled="!isUsername || !isPassword || !isCheckPassword">
              {{$t('register.register')}}
            </el-button>
          </el-form-item>
        </el-form>
        <dl>
          <dt></dt>
          <dd>
            {{$t('register.haveAccounts')}}<el-button type="text" @click="goLogin">{{$t('register.goLogin')}}</el-button>
          </dd>
        </dl>
      </div>
      <copy-right></copy-right>
    </div>
  </div>
</template>

<script>
import { isvalidUsername, isvalidUserpassword } from 'common/js/validate'
import Advert from 'base/advert'
import Language from 'base/language'
import copyRight from 'base/copyright'
import { loginByUsername } from 'api/login'
import { setToken } from 'common/js/auth'

export default {
  name: 'register',
  data () {
    const validateUsername = (rule, value, callback) => {
      if (!isvalidUsername(value)) {
        this._data.isUsername = false
        callback(new Error(' '))
      } else {
        this._data.isUsername = true
        callback()
      }
    }
    const validatePass = (rule, value, callback) => {
      if (!isvalidUserpassword(value)) {
        this._data.isPassword = false
        callback(new Error(' '))
      } else {
        this._data.isPassword = true
        callback()
      }
    }
    const validateCheckPass = (rule, value, callback) => {
      if (!isvalidUserpassword(value)) {
        this._data.isCheckPassword = false
        callback(new Error(' '))
      } else if(value !== this.registerForm.password) {
        this._data.isCheckPassword = false
        callback(new Error(' '))
      } else {
        this._data.isCheckPassword = true
        callback()
      }
    }
    return {
      registerForm: {
        username: '',
        password: '',
        checkPassword: ''
      },
      registerType: {
        username: '',
        password: '',
        checkPassword: ''
      },
      registerRules: {
        username: [
          {required: true, trigger: 'blur', validator: validateUsername},
          {required: true, trigger: 'change', validator: validateUsername},
        ],
        password: [
          {required: true, trigger: 'blur', validator: validatePass},
          {required: true, trigger: 'change', validator: validatePass}
        ],
        checkPassword: [
          {required: true, trigger: 'blur', validator: validateCheckPass},
          {required: true, trigger: 'change', validator: validateCheckPass}
        ]
      },
      loading: false,
      pwdType: 'password',

      isUsername: '',
      isPassword: '',
      isCheckPassword: '',
      isDisabled: true
    }
  },
  created () {
    console.log(this.$i18n)
  },
  methods: {
    showPwd () {
      if (this.pwdType === 'password') {
        this.pwdType = ''
      } else {
        this.pwdType = 'password'
      }
    },
    handleRegister () {
      this.$refs.registerForm.validate(valid => {
        if (valid) {
          this.$router.push({path: '/code'})
          // this.loading = true
          // let userInfo = Object.assign({}, this.loginForm)
          // 
          // loginByUsername({userInfo}).then((res) => {
          //   this.loading = false
          //   this.$store.commit('SET_TOKEN', 'admin')
          //   // 
          //   setToken('admin')
          //   this.$router.push({path: '/'})
          // }).catch(() => {
          //   this.loading = false
          // })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 
    goLogin() {
      this.$router.go('-1')
    }
  },
  components: {
    Advert,
    Language,
    copyRight
  }
}


</script>

<style rel="stylesheet/scss" lang="scss">
@import "common/styles/variable.scss";
$bg: #2d3a4b;
$light_gray: #eee;

/* reset element-ui css */
.register-container {
  .register-form {
    .el-form-item {
      border: none;
      margin-bottom: 8px;
      &:nth-of-type(3) {
        margin-bottom: 36px;
      }
      &:nth-of-type(4) {
        margin-bottom: 2px;
      }
      &.noLabel label {
        visibility: hidden;
      }
      & label::before {
        display: none;
      }
      .el-input {
        display: inline-block;
        height: 36px;
        input {
          background: transparent;
          border: 0px;
          -webkit-appearance: none;
          border-radius: 0px;
          padding: 9px 5px 9px 0;
          color: $gray_dark;
          height: 36px;
          font-size: 18px;
          border-bottom: 1px solid $gray_line;
          &::-webkit-input-placeholder {
            color: $gray_placeholder;
          }
        }
      }
      .el-input__inner:focus {
        border-bottom: 1px solid $gray_line_on;
      }
      label {
        color: $gray_placeholder;
        font-size: 12px;
        line-height: 12px;
        padding: 8px 0 0 0;
      }
      &.is-error {
        label {
          visibility: visible;
          color: $red;
        }
        .el-input__inner {
          border-bottom: 1px solid $red;
        }
        .el-input__inner:focus {
          border-bottom: 1px solid $gray_line_on;
        }
      }
      &.onFocus {
        label {
          visibility: visible;
          color: $gray_line_on;
        }
      }
      .el-button {
        padding: 15px 20px;
        font-size: 18px;
        background-color: $gray_line_on;
        border: none;
        border-radius: 4px;
      }
      .el-button--medium {
        font-size: 18px;
        color: $white;
      }
      .is-disabled {
        background: $gray_line;
        color: $gray_light;
      }
    }
  }
  .register-inner {
    .el-button--medium {
      font-size: 12px;
      color: $blue;
    }
  }
}

</style>

<style rel="stylesheet/scss" lang="scss" scoped>
@import "common/styles/variable.scss";
.baima-container {
  position: fixed;
  height: 100%;
  width: 100%;
  display: flex;
  .register-container {
    flex: 1;
    position: relative;
    .register-inner {
      position: absolute;
      left: 50%;
      right: 0;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 366px;
      margin: 0 auto;
      .register-form {
      }
      .show-pwd {
        position: absolute;
        right: 0;
        top: 10px;
        height: 24px;
        cursor: pointer;
        user-select: none;
      }
      dl {
        display: flex;
        justify-content: space-between;
        font-size: 12px;
        dt {
          flex: 1;
        }
        dd {
          flex: 1;
          color: $gray_light;
          text-align: right;
        }
      }
    }
  }
}
</style>



