import qs from 'qs'
import request from 'common/js/request'
import {hex_md5} from 'common/js/md5'
import axios from 'axios'

const commonParams = {
  timestamp: new Date().getTime(),
  requestType: 1
}

const formatStr = (data) => {
  let str = ''
  Object.keys(data).forEach(key => {
    str += `${key}=${data[key]}`
  })
  return hex_md5(str)
}

// 
export function loginByUsername({userInfo}) {

  const data = Object.assign({}, commonParams, {
    username: userInfo.username,
    pass: userInfo.password
  })

  return request({
    url: '/login',
    method: 'post',
    data: qs.stringify(Object.assign(data, {
      sign:formatStr(data)
    }))
  })
}


// devServer
export function loginByUsername2({userInfo}) {

  const data = Object.assign({}, commonParams, {
    username: userInfo.username,
    pass: userInfo.password
  })

  let url = '/api/login'

  return axios.post(url, qs.stringify(Object.assign(data, {
    sign: formatStr(data)
  }))).then((res) => {
    return Promise.resolve(res.data)
  })
}



