import axios from 'axios'
import { Message, MessageBox } from 'element-ui'
import store from '@/store'
import { getToken } from 'common/js/auth'

// axios
const service = axios.create({
  // baseURL: 'http://localhost:8080/api/api/login',
  baseURL: process.env.API_ROOT, // apibase_url
  timeout: 5000, // 
})

// request
service.interceptors.request.use(config => {
  console.log(1, 'request')
  if (store.getters.token) {
    config.headers['X-Token'] = getToken() // token 
  }
  return config
}, error => {
  console.log(2, 'request')
  console.log(error) // for debug
  Promise.reject(error)
})

// respone
service.interceptors.response.use(
  response => {
    console.log(1, 'respone')
    return response
  },
  /**
   * responsecodecode
   * xmlhttprequest  error
   */
    //  const res = response.data;
    //     if (res.code !== 20000) {
    //       Message({
    //         message: res.message,
    //         type: 'error',
    //         duration: 5 * 1000
    //       });
    //       // 50008:token; 50012:;  50014:Token ;
    //       if (res.code === 50008 || res.code === 50012 || res.code === 50014) {
    //         MessageBox.confirm('', '', {
    //           confirmButtonText: '',
    //           cancelButtonText: '',
    //           type: 'warning'
    //         }).then(() => {
    //           store.dispatch('FedLogOut').then(() => {
    //             location.reload();// vue-router bug
    //           });
    //         })
    //       }
    //       return Promise.reject('error');
    //     } else {
    //       return response.data;
    //     }
  error => {
    console.log(2, 'respone', JSON.parse(JSON.stringify(error)))
    console.log('err' + error) // for debug
    Message({
      message: error.message,
      type: 'error',
      duration: 5 * 1000
    })
    return Promise.reject(error)
  })

export default service



